.horizontally-centered {
	display: flex;
	align-items: center;
	flex-direction: column;
	padding: 60px 24px;
}
#details-page {
	display: flex;
	align-items: flex-start;
	margin-top: 60px;
	padding-bottom: 48px;
}
#places-image {
	img {
		height: 100%;
		border-radius: 20px;
	}
	height: 320px;
}
#place-details {
	p {
		font-size: 16px;
	}
}
#mini-map {
	width: 100%;
	height: 300px;
	border-radius: 20px;
}
#review {
	textarea {
		font-family: "Poppins", sans-serif;
		outline: none;
		font-size: 12px;
		resize: none;
		border: none;
		border-radius: 8px;
		box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.1);
		width: 100%;
		padding: 8px;
		margin: 8px 0;
		height: 120px;
		color: rgba(0, 0, 0, 0.84);
		margin-bottom: 16px;
	}
	button[type="submit"] {
		margin-top: 16px;
		border-radius: 16px;
		cursor: pointer;
		font-size: 14px;
		padding: 12px;
		width: 100%;
	}
	width: 440px;
	margin-left: 128px;
	padding-left: 48px;
	font-size: 16px;
	border-left: 1px solid rgba(0, 0, 0, 0.56);
	border-radius: 0;
	#existing-reviews {
		margin-top: 32px;
		color: rgba(0, 0, 0, 0.56);
		p {
			font-size: 13px;
			margin: 16px 0;
		}
	}
	form {
		width: 100%;
		padding: 0;
		background: none;
		align-items: flex-start;
	}
	h3 {
		margin-bottom: 8px;
		font-size: 16px;
		color: rgba(0, 0, 0, 0.84);
	}
}
.username {
	font-weight: bold;
}
#left-side {
	width: 560px;
}
.details-divider {
	border-top-color: rgb(120, 120, 120);
	border-top-width: 1px;
	border-top-style: solid;
	margin: 40px 0;
}
#details-misc {
	display: flex;
	align-items: flex-start;
	justify-content: flex-end;
	p {
		font-size: 14px;
		margin: 8px 8px 0 0;
		color: rgba(0, 0, 0, 0.54);
	}
}
p#details-address {
	font-size: 14px;
	margin: 0;
	color: rgba(0, 0, 0, 0.54);
}
#details-description {
	margin: 8px 0;
}
