#title-error {
	font-weight: 700;
	font-size: 70px;
	letter-spacing: 4px;
}
#button-home {
	border-radius: 10px;
	background-color: #1A2F3D;
	font-size: 14px;
	padding: 12px 45px;
	letter-spacing: 1.6px;
	transition: transform 80ms ease-in;
	width: 30%;
	height: 42px;
	text-align: center;
	margin: 10px;
	a {
		text-decoration: none;
		color: #F1F1F1;
	}
	&:hover {
		background: linear-gradient(to bottom right, #1A2F3D, #264255);
		cursor: pointer;
		font-size: 16px;
	}
}
#p-error {
	margin: 10px 0;
}
#gif-error {
	border-radius: 24px;
	margin: 32px;
}
.error-distance {
	margin-top: 60px;
}
footer {
	z-index: 1001;
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	position: fixed;
	bottom: 0;
	height: 60px;
	color: white;
	background-color: #1A2F3D;
	box-shadow: 0px -5px 5px #757575;
}
.linkedin-ref {
	color: #00C6BC;
	text-decoration: none;
	&:hover {
		color: #00C6BC;
		text-decoration: underline;
	}
}
